import axios from '../axiosWrapper';

let prefix = process.env.API_ROOT

export const add = (params) => { return axios.post(`${prefix}/manager/classify/add.json`, params); };

export const update = (id, params) => { return axios.post(`${prefix}/manager/classify/${id}/update.json`, params); };

export const list = (params) => { return axios.post(`${prefix}/manager/classify/list.json`, params); };

export const del = (id) => { return axios.post(`${prefix}/manager/classify/${id}/del.json`); };

export const detail = (id) => { return axios.get(`${prefix}/manager/classify/${id}/detail.json`); };

export const classifys = () => { return axios.post(`${prefix}/manager/classify/classifys.json`); };


