import Login from './views/Login.vue'
import NotFound from './views/404.vue'
import Home from './views/Home.vue'
import Main from './views/Main.vue'

import Board from './views/board/Board.vue'

import User from './views/user/User.vue'
import UserAdd from './views/user/UserAdd.vue'
import UserUpdate from './views/user/UserUpdate.vue'

import Role from './views/role/Role.vue'


import Version from './views/version/Version.vue'
import VersionAdd from './views/version/VersionAdd.vue'
import VersionUpdate from './views/version/VersionUpdate.vue'

import Advertisement from './views/advertisement/Advertisement.vue'
import AdvertisementAdd from './views/advertisement/AdvertisementAdd.vue'
import AdvertisementUpdate from './views/advertisement/AdvertisementUpdate.vue'

import Movie from './views/movie/Movie.vue'
import MovieAdd from './views/movie/MovieAdd.vue'
import MovieUpdate from './views/movie/MovieUpdate.vue'

import RegistrationCode from './views/registrationCode/RegistrationCode.vue'
import RegistrationCodeAdd from './views/registrationCode/RegistrationCodeAdd.vue'
import RegistrationCodeUpdate from './views/registrationCode/RegistrationCodeUpdate.vue'

import Config from './views/config/Config.vue'
import ConfigAdd from './views/config/ConfigAdd.vue'
import ConfigUpdate from './views/config/ConfigUpdate.vue'

import Classify from './views/classify/Classify.vue'
import ClassifyAdd from './views/classify/ClassifyAdd.vue'
import ClassifyUpdate from './views/classify/ClassifyUpdate.vue'

let routes = [{
    path: '/login',
    component: Login,
    name: '',
},
{
    path: '/404',
    component: NotFound,
    name: '',
},
{
    path: '/',
    component: Home,
    redirect: '/board',
    name: '工作看板',
    children: [{
        path: '/board',
        component: Board,
        name: '工作看板',
        children: [
        ]
    }]
},
{
    path: '/',
    component: Home,
    name: '系统管理',
    children: [{
            path: '/user',
            component: User,
            name: '用户管理',
            children: [{
                path: 'add',
                component: UserAdd,
                name: 'userAdd',
                alias: '添加用户'
            },
            {
                path: ':id/update',
                component: UserUpdate,
                name: 'userUpdate',
                alias: '更新用户'
            }]
        }, {
            path: '/role',
            component: Role,
            name: '角色管理',
            children: []
        }
    ]
}, 
{
    path: '/',
    component: Home,
    name: '业务管理',
    children: [{
            path: '/version',
            component: Version,
            name: '系统版本',
            children: [{
                path: 'add',
                component: VersionAdd,
                name: 'versionAdd',
                alias: '添加版本'
            },
            {
                path: ':id/update',
                component: VersionUpdate,
                name: 'versionUpdate',
                alias: '更新版本'
            }]
        }, {
            path: '/advertisement',
            component: Advertisement,
            name: '广告管理',
            children: [{
                path: 'add',
                component: AdvertisementAdd,
                name: 'advertisementAdd',
                alias: '添加广告'
            },
            {
                path: ':id/update',
                component: AdvertisementUpdate,
                name: 'advertisementUpdate',
                alias: '更新广告'
            }]
        }, {
            path: '/movie',
            component: Movie,
            name: '视频管理',
            children: [{
                path: 'add',
                component: MovieAdd,
                name: 'movieAdd',
                alias: '添加视频'
            },
            {
                path: ':id/update',
                component: MovieUpdate,
                name: 'movieUpdate',
                alias: '更新视频'
            }]
        }, {
            path: '/registrationCode',
            component: RegistrationCode,
            name: '注册码管理',
            children: [{
                path: 'add',
                component: RegistrationCodeAdd,
                name: 'registrationCodeAdd',
                alias: '添加注册码'
            },
            {
                path: ':id/update',
                component: RegistrationCodeUpdate,
                name: 'registrationCodeUpdate',
                alias: '更新注册码'
            }]
        }, {
            path: '/config',
            component: Config,
            name: '配置管理',
            children: [{
                path: 'add',
                component: ConfigAdd,
                name: 'configAdd',
                alias: '添加'
            },
            {
                path: ':id/update',
                component: ConfigUpdate,
                name: 'configUpdate',
                alias: '更新'
            }]
        }, {
            path: '/classify',
            component: Classify,
            name: '分类管理',
            children: [{
                path: 'add',
                component: ClassifyAdd,
                name: 'classifyAdd',
                alias: '添加'
            },
            {
                path: ':id/update',
                component: ClassifyUpdate,
                name: 'classifyUpdate',
                alias: '更新'
            }]
        }
    ]
}, 
{
    path: '*',
    redirect: {
        path: '/404'
    }
}
];

export default routes;


