import axios from '../axiosWrapper';

let prefix = process.env.API_ROOT

export const add = (params) => { return axios.post(`${prefix}/manager/registrationCode/add.json`, params); };

export const update = (id, params) => { return axios.post(`${prefix}/manager/registrationCode/${id}/update.json`, params); };

export const list = (params) => { return axios.post(`${prefix}/manager/registrationCode/list.json`, params); };

export const del = (id) => { return axios.post(`${prefix}/manager/registrationCode/${id}/del.json`); };

export const detail = (id) => { return axios.get(`${prefix}/manager/registrationCode/${id}/detail.json`); };


