<template>
	<el-dialog title="新增" :visible.sync="showDialog" size="small" @close="closeDialog">
		<el-form :model="formEntity" label-width="100px" label-suffix=":">
			<el-form-item label="标题">
				<el-input v-model="formEntity.title"></el-input>
			</el-form-item>
			<el-form-item label="内容">
				<el-input v-model="formEntity.content"></el-input>
			</el-form-item>
			<el-form-item label="状态">
				<el-select filterable v-model="formEntity.state">
					<el-option v-for="item in stateList" :value="item.code" :label="item.name" :key="item.code" />
				</el-select>
			</el-form-item>
			<el-form-item label="分类">
				<el-select v-model="formEntity.classifys" multiple placeholder="请选择视频分类">
					<el-option
						v-for="item in classifys"
						:key="item.id"
						:label="item.name"
						:value="item.id">
					</el-option>
				</el-select>

			</el-form-item>
			<el-form-item label="封面">
				<el-upload
				class="upload-demo"
				:action="fileUploadUrl"
				:data="movieImageRequestBody"
				:on-success="imageUploadSuccess"
				:file-list="fileList2"
				list-type="picture">
				<el-button size="small" type="primary">点击上传</el-button>
				<div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
			</el-upload>
			</el-form-item>
			<el-form-item label="视频">
				<el-upload
					class="upload-demo"
					ref="upload"
					:action="fileUploadUrl"
					:data="movieFileRequestBody"
					:on-success="fileUploadSuccess"
					:file-list="fileList"
					:auto-upload="false">
					<el-button slot="trigger" size="small" type="primary">选取文件</el-button>
					<el-button style="margin-left: 10px;" size="small" type="success" @click="submitUpload">上传到服务器</el-button>
					<div slot="tip" class="el-upload__tip">只能上传avi/mp4文件，且不超过50M</div>
				</el-upload>
			</el-form-item>
			
		</el-form>
		<div slot="footer" class="dialog-footer">
			<el-button size="small" @click="closeDialog">返 回</el-button>
			<el-button size="small" type="primary" @click="handleAdd">确 定</el-button>
		</div>
	</el-dialog>
</template>

<style lang="scss" scoped>

</style>

<script>
import axios from 'axios';
import api from '../../api';
import util from '../../common/js/util';
import commons from '../../mixins/commons';


export default {
	mixins: [
		commons
	],
	data() {
		return {
			showDialog: true,
			fileUploadUrl: process.env.API_ROOT + '/fileUpload',
			movieImageRequestBody:{type : 'movie', subType: 'image'},
			movieFileRequestBody:{type : 'movie', subType: 'file'},
			stateList: [{code : 0, name : "初始"}, {code : 1, name : "待审核"}, {code : 2, name : "审核中"}, {code : 3, name : "审核通过"}, {code : 4, name : "审核不通过"}],
			formEntity: {
				title: null,
				content: null,
				state: 0
			},
			classifys:[],
		}
	},
	created() {
		api.classify.classifys().then(res => {
			this.classifys = res.data.data;
		});
	},
	watch: {
		
	},
	methods: {
		closeDialog() {
			//父页面更新列表
			this.$parent.loadPage();
			this.$router.push({ path: '/movie' });
		},
		handleAdd() {			
			api.movie.add(this.formEntity).then(res => {
				this.closeDialog();
			})
		},
		imageUploadSuccess(response, file, fileList) {
			this.formEntity.coverPath = response.data;
		},
		submitUpload() {
        	this.$refs.upload.submit();
      	},
		fileUploadSuccess(response, file, fileList) {
			this.formEntity.videoPath = response.data;
		},
	}
}
</script>



