<template>
	<el-dialog title="更新用户" :visible.sync="showDialog" size="small" @close="closeDialog">
		<el-form :model="formEntity" label-width="100px" label-suffix=":">
			<el-form-item label="名称">
				<el-input v-model="formEntity.name"></el-input>
			</el-form-item>
			<el-form-item label="序号">
				<el-input v-model="formEntity.sequence"></el-input>
			</el-form-item>
		</el-form>
		<div slot="footer" class="dialog-footer">
			<el-button size="small" @click="closeDialog">返 回</el-button>
			<el-button size="small" type="primary" @click="handleUpdate">确 定</el-button>
		</div>
	</el-dialog>
</template>

<style lang="scss" scoped>

</style>

<script>
import axios from 'axios';
import api from '../../api';
import util from '../../common/js/util';
import commons from '../../mixins/commons';


export default {
	mixins: [
		commons
	],
	data() {
		return {
			showDialog: true,
			formEntity: {
				name: null,
				sequence: null,
			},
		}
	},
	created() {
		api.classify.detail(this.$route.params.id).then(res => {
			this.formEntity = res.data.data;
		});
	},
	watch: {
		
	},
	methods: {
		closeDialog() {
			this.$parent.loadPage();
			this.$router.push({ path: '/classify' });
		},
		handleUpdate() {
			api.classify.update(this.formEntity.id, this.formEntity).then(res => {
				this.closeDialog();
			})
		},
	}
}
</script>



