<template>
	<el-dialog title="生成注册码" :visible.sync="showDialog" size="small" @close="closeDialog">
		<el-form :model="formEntity" label-width="100px" label-suffix=":">
			<el-form-item label="生成个数">
				<el-input v-model="formEntity.total"></el-input>
			</el-form-item>
			<el-form-item label="有效天数">
				<el-input v-model="formEntity.days"></el-input>
			</el-form-item>
			<el-form-item label="状态">
				<el-select filterable v-model="formEntity.state">
					<el-option v-for="item in stateList" :value="item.code" :label="item.name" :key="item.code" />
				</el-select>
			</el-form-item>
		</el-form>
		<div slot="footer" class="dialog-footer">
			<el-button size="small" @click="closeDialog">返 回</el-button>
			<el-button size="small" type="primary" @click="handleAdd">确 定</el-button>
		</div>
	</el-dialog>
</template>

<style lang="scss" scoped>

</style>

<script>
import axios from 'axios';
import api from '../../api';
import util from '../../common/js/util';
import commons from '../../mixins/commons';


export default {
	mixins: [
		commons
	],
	data() {
		return {
			showDialog: true,
			stateList: [{code : 1, name : "封停"}, {code : 2, name : "可用"}],
			formEntity: {
				total: 1,
				days: 1,
				state: 1
			},
		}
	},
	created() {
		
	},
	watch: {
		
	},
	methods: {
		closeDialog() {
			//父页面更新列表
			this.$parent.loadPage();
			this.$router.push({ path: '/registrationCode' });
		},
		handleAdd() {			
			api.registrationCode.add(this.formEntity).then(res => {
				this.closeDialog();
			})
		},
	}
}
</script>



