<template>
	<div>
    <div class="queryForm">
			<el-form :inline="true" :model="queryParam" class="demo-form-inline" label-suffix=":" label-width="100px">
				<el-form-item label="广告类型">
					<el-select filterable v-model="queryParam.query.type">
						<el-option value="" label="所有" />
						<el-option v-for="item in typeList" :value="item.code" :label="item.name" :key="item.code" />
					</el-select>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" size="small" @click="loadPage">查询</el-button>
					<el-button type="primary" size="small" @click="reset">重置</el-button>
				</el-form-item>
			</el-form>
		</div>
    <el-button type="primary" size="small" @click="$router.push({ name: 'advertisementAdd' })">新增</el-button>
		<el-table :data="pageInfo.tableList" highlight-current-row stripe border max-height="640" v-loading="listLoading" @selection-change="selsChange" style="width: 100%;">
			<el-table-column type="index" width="60" align="center">
			</el-table-column>
      <el-table-column prop="id" label="ID" min-width="40">
			</el-table-column>
      <el-table-column prop="title" label="标题" min-width="100">
			</el-table-column>
      <el-table-column prop="content" label="内容" min-width="100">
			</el-table-column>
      <el-table-column prop="type" label="广告类型" min-width="70">
        <template slot-scope="scope">
          <el-tag v-if="scope.row.type==1" type="1">开机广告</el-tag>
          <el-tag v-if="scope.row.type==2" type="2">首页广告</el-tag>
          <el-tag v-if="scope.row.type==3" type="3">详情页上方广告</el-tag>
          <el-tag v-if="scope.row.type==6" type="6">详情页下方广告</el-tag>
          <el-tag v-if="scope.row.type==4" type="4">详情页大广告</el-tag>
          <el-tag v-if="scope.row.type==5" type="5">首页轮播广告</el-tag>
        </template>
			</el-table-column>
      <el-table-column prop="imageUrl" label="图片" width="120">
        <template slot-scope="scope">
          <img  :src="scope.row.imageUrl" alt="" style="width: 100px;height: 100px">
        </template>
      </el-table-column>
      <el-table-column prop="auditStatus" label="审核状态" min-width="70">
        <template slot-scope="scope">
          <el-tag v-if="scope.row.auditStatus==1" auditStatus="1">创建</el-tag>
          <el-tag v-if="scope.row.auditStatus==2" auditStatus="2">待审核</el-tag>
          <el-tag v-if="scope.row.auditStatus==3" auditStatus="3">审核通过</el-tag>
        </template>
			</el-table-column>
      <el-table-column prop="state" label="投放状态" min-width="70">
        <template slot-scope="scope">
          <el-tag v-if="scope.row.state==1" state="1">未开始投放</el-tag>
          <el-tag v-if="scope.row.state==2" state="2">正在投放</el-tag>
          <el-tag v-if="scope.row.state==3" state="3">暂停投放</el-tag>
          <el-tag v-if="scope.row.state==4" state="4">停止投放</el-tag>
        </template>
			</el-table-column>
      <el-table-column prop="link" label="链接" width="120">
        <template slot-scope="scope">
          <a  :href="scope.row.link">链接</a>
        </template>
      </el-table-column>
			<el-table-column prop="createTime" label="创建时间" min-width="120" :formatter="dateFormat" sortable align="center">
			</el-table-column>
			<el-table-column prop="updateTime" label="更新时间" min-width="120" :formatter="dateFormat" sortable align="center">
			</el-table-column>
			<el-table-column label="操作" width="320" align="center">
				<template slot-scope="scope">
					<el-button type="primary" size="small" @click="$router.push({name:'advertisementUpdate',params: {id:scope.row.id}})">编辑</el-button>
					<el-button type="danger" size="small" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
				</template>
			</el-table-column>
		</el-table>

		<div class="paginationSection" style="float:right">
			<el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pageInfo.page.currentPage" :page-sizes="[10, 15, 20, 30, 40]" :page-size="pageInfo.page.pageSize" layout="total, sizes, prev, pager, next, jumper" :total="pageInfo.page.totalNum">
			</el-pagination>
		</div>
		<router-view></router-view>
	</div>
</template>

<style lang="scss" scoped>

</style>

<script>
import axios from "axios";
import api from "../../api";
import util from "../../common/js/util";
import commons from "../../mixins/commons";

export default {
  mixins: [commons],
  data() {
    return {
      typeList: [{code : 1, name : "开机广告"}, {code : 2, name : "首页广告"}, {code : 3, name : "详情页上方广告"}, {code : 6, name : "详情页下方广告"}, {code : 4, name : "详情页大广告"}, {code : 5, name : "首页轮播广告"}],
      pageInfo: {
        tableList: [],
        page: {
          currentPage: 1,
          pageSize: 15,
          totalNum: 0
        }
      },
      listLoading: false,
      sels: [],
      //检索参数
      queryParam: storeSession.get("ADVERTISEMENT-QUERY") || {
        query: {
        },
        page: {
          currentPage: 1,
          pageSize: 15,
          totalNum: 0
        }
      }
    };
  },
  created() {
    this.loadPage();
  },
  watch: {
    queryParam: {
      handler(curVal, oldVal) {
        //清理延时任务
        clearTimeout(this.timeoutTask);
        //启动延时任务
        this.timeoutTask = setTimeout(() => {
          this.loadPage();
        }, 400);
      },
      deep: true
    }
  },
  methods: {
    selsChange(sels) {
      this.sels = sels;
    },
    loadPage() {
      storeSession.set("ADVERTISEMENT-QUERY", this.queryParam);
      api.advertisement.list(this.queryParam).then(res => {
        this.pageInfo.tableList = res.data.data.list;
        this.pageInfo.page = res.data.data.page;
      });
    },
    handleSizeChange(val) {
      this.queryParam.page.pageSize = val;
    },
    handleCurrentChange(val) {
      this.queryParam.page.currentPage = val;
    },
    handleDelete(index, row) {
      this.$confirm("确认删除", "警告").then(() => {
        api.advertisement.del(row.id).then(res => {
          this.loadPage();
        });
      });
    },
    reset() {
      this.queryParam = {
        query:{
        },
        page: {
          currentPage: 1,
          pageSize: 15,
          totalNum: 0
        }
      };
    }
  }
};
</script>



