<template>
	<el-dialog title="更新用户" :visible.sync="showDialog" size="small" @close="closeDialog">
		<el-form :model="formEntity" label-width="100px" label-suffix=":">
			<el-form-item label="终端类型">
				<el-select filterable v-model="formEntity.type">
					<el-option v-for="item in typeList" :value="item.code" :label="item.name" :key="item.code" />
				</el-select>
			</el-form-item>
			<el-form-item label="标题">
				<el-input v-model="formEntity.name"></el-input>
			</el-form-item>
			<el-form-item label="版本号">
				<el-input v-model="formEntity.versionCode"></el-input>
			</el-form-item>
			<el-form-item label="版本名">
				<el-input v-model="formEntity.versionName"></el-input>
			</el-form-item>
			<el-form-item label="更新内容">
				<el-input v-model="formEntity.content"></el-input>
			</el-form-item>
			<el-form-item label="封面">
				<el-upload
				class="upload-demo"
				ref="imageUpload"
				:action="fileUploadUrl"
				:data="versionImageRequestBody"
				:on-success="imageUploadSuccess"
				:file-list="imageFileList"
				:on-remove="handleRemoveFile"
				list-type="picture">
				<el-button size="small" type="primary">点击上传</el-button>
				<div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
			</el-upload>
			</el-form-item>
			<el-form-item label="更新文件">
				<el-upload
					class="upload-demo"
					ref="upload"
					:action="fileUploadUrl"
					:data="versionFileRequestBody"
					:on-success="fileUploadSuccess"
					:file-list="fileList"
					:on-remove="handleRemoveFile"
					:auto-upload="false">
					<el-button slot="trigger" size="small" type="primary">选取文件</el-button>
					<el-button style="margin-left: 10px;" size="small" type="success" @click="submitUpload">上传到服务器</el-button>
					<div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
				</el-upload>
			</el-form-item>
		</el-form>
		<div slot="footer" class="dialog-footer">
			<el-button size="small" @click="closeDialog">返 回</el-button>
			<el-button size="small" type="primary" @click="handleUpdate">确 定</el-button>
		</div>
	</el-dialog>
</template>

<style lang="scss" scoped>

</style>

<script>
import axios from 'axios';
import api from '../../api';
import util from '../../common/js/util';
import commons from '../../mixins/commons';


export default {
	mixins: [
		commons
	],
	data() {
		return {
			showDialog: true,
			imageFileList: [],
			fileList: [],
			fileUploadUrl: process.env.API_ROOT + '/fileUpload',
			versionImageRequestBody: {type : 'version', subType: 'image'},
			versionFileRequestBody: {type : 'version', subType: 'file'},
			typeList: [{code : 1, name : "IOS"}, {code : 2, name : "Android"}],
			formEntity: {
				name: null,
				versionCode: null,
				versionName: null,
				content: null,
				type: 1
			},
		}
	},
	created() {
		api.version.detail(this.$route.params.id).then(res => {
			this.formEntity = res.data.data;
			this.imageFileList = [{name: this.formEntity.imagePath, url: this.formEntity.imageUrl}];
			this.fileList = [{name : this.formEntity.downloadPath}];
		});
	},
	watch: {
		
	},
	methods: {
		closeDialog() {
			this.$parent.loadPage();
			this.$router.push({ path: '/version' });
		},
		handleUpdate() {
			api.version.update(this.formEntity.id, this.formEntity).then(res => {
				this.closeDialog();
			})
		},
		imageUploadSuccess(response, file, fileList) {
			this.formEntity.imagePath = response.data;
		},
		submitUpload() {
        	this.$refs.upload.submit();
      	},
		fileUploadSuccess(response, file, fileList) {
			this.formEntity.downloadPath = response.data;
		},
		handleRemoveFile(file, fileList) {
			
		}
	}
}
</script>



