<template>
	<el-dialog title="新增" :visible.sync="showDialog" size="small" @close="closeDialog">
		<el-form :model="formEntity" label-width="100px" label-suffix=":">
			<el-form-item label="广告类型">
				<el-select filterable v-model="formEntity.type">
					<el-option v-for="item in typeList" :value="item.code" :label="item.name" :key="item.code" />
				</el-select>
			</el-form-item>
			<el-form-item label="标题">
				<el-input v-model="formEntity.title"></el-input>
			</el-form-item>
			<el-form-item label="内容">
				<el-input v-model="formEntity.content"></el-input>
			</el-form-item>
			<el-form-item label="链接">
				<el-input v-model="formEntity.link"></el-input>
			</el-form-item>
			<el-form-item label="图片">
				<el-upload
				class="upload-demo"
				:action="fileUploadUrl"
				:data="imageRequestBody"
				:on-success="imageUploadSuccess"
				:file-list="fileList2"
				list-type="picture">
				<el-button size="small" type="primary">点击上传</el-button>
				<div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
			</el-upload>
			</el-form-item>
		</el-form>
		<div slot="footer" class="dialog-footer">
			<el-button size="small" @click="closeDialog">返 回</el-button>
			<el-button size="small" type="primary" @click="handleAdd">确 定</el-button>
		</div>
	</el-dialog>
</template>

<style lang="scss" scoped>

</style>

<script>
import axios from 'axios';
import api from '../../api';
import util from '../../common/js/util';
import commons from '../../mixins/commons';


export default {
	mixins: [
		commons
	],
	data() {
		return {
			showDialog: true,
			fileUploadUrl: process.env.API_ROOT + '/fileUpload',
			imageRequestBody:{type : 'advertisement', subType: 'image'},
			typeList: [{code : 1, name : "开机广告"}, {code : 2, name : "首页广告"}, {code : 3, name : "详情页上方广告"}, {code : 6, name : "详情页下方广告"}, {code : 4, name : "详情页大广告"}, {code : 5, name : "首页轮播广告"}],
			formEntity: {
				title: null,
				content: null,
				link: null,
				imagePath: null,
				type: 1,
				auditStatus: 1,
				state: 1
			},
		}
	},
	created() {
		
	},
	watch: {
		
	},
	methods: {
		closeDialog() {
			//父页面更新列表
			this.$parent.loadPage();
			this.$router.push({ path: '/advertisement' });
		},
		handleAdd() {			
			api.advertisement.add(this.formEntity).then(res => {
				this.closeDialog();
			})
		},
		imageUploadSuccess(response, file, fileList) {
			this.formEntity.imagePath = response.data;
		},
		submitUpload() {
        	this.$refs.upload.submit();
      	},
	}
}
</script>



