<template>
	<el-dialog title="新增" :visible.sync="showDialog" size="small" @close="closeDialog">
		<el-form :model="formEntity" label-width="100px" label-suffix=":">
			<el-form-item label="组名">
				<el-input v-model="formEntity.group"></el-input>
			</el-form-item>
			<el-form-item label="key">
				<el-input v-model="formEntity.key"></el-input>
			</el-form-item>
			<el-form-item label="value">
				<el-input v-model="formEntity.value"></el-input>
			</el-form-item>
			<el-form-item label="备注">
				<el-input v-model="formEntity.description"></el-input>
			</el-form-item>
		</el-form>
		<div slot="footer" class="dialog-footer">
			<el-button size="small" @click="closeDialog">返 回</el-button>
			<el-button size="small" type="primary" @click="handleAdd">确 定</el-button>
		</div>
	</el-dialog>
</template>

<style lang="scss" scoped>

</style>

<script>
import axios from 'axios';
import api from '../../api';
import util from '../../common/js/util';
import commons from '../../mixins/commons';


export default {
	mixins: [
		commons
	],
	data() {
		return {
			showDialog: true,
			formEntity: {
				group: null,
				key: null,
				value: null,
				description: null,
			},
		}
	},
	created() {
		
	},
	watch: {
		
	},
	methods: {
		closeDialog() {
			//父页面更新列表
			this.$parent.loadPage();
			this.$router.push({ path: '/config' });
		},
		handleAdd() {			
			api.config.add(this.formEntity).then(res => {
				this.closeDialog();
			})
		},
	}
}
</script>



